import back from '@/components/back'
export default {
	components:{
		back
	},
	data () {
		return {
			top: (88 / 75 * 1 + 'rem'),
			arr: [],
			show: false,
			FieldType: '',
			MFItemId: '',
			deleteShow: false,
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
			h404: true,
			OrgIds: this.$store.state.userInfo == null ? 1 : this.$store.state.userInfo.Data.User.OrgId[0],
			start: this.$route.params.start,
			id: this.$route.query.id,
			title: '编辑分组',
			manStart: false,
			MFItemArr: [],
			MFItemName:'',
			mFNames:this.$store.state.mFItemNames,
			mName:[],
			isImEntry:this.$route.query.isImEntry,
			user: this.$store.state.userInfo,
            Patient: this.$store.state.patient,
		}
	},
	created(){
		this.$bridge.callhandler('printdata','初始进入0')
		if(this.start == '1'){
			this.title = '编辑分组'
			this.manStart = true
		}else{
			this.title = '选择分组'
			this.manStart = false
		}
		if(this.mFNames){
			if(this.mFNames.indexOf(",") == -1){
				this.mName.push(this.mFNames)
			}else{
				this.mName = this.mFNames.split(',')
			}
		}
		if(this.start != '1' && !this.mFNames){
			this.$get('PlatFormAPI/DoctorPatient/QueryDoctorPatientSummary',{
				PatientId:this.Patient.PatientId,
	            DrId:this.user.Data.User.DoctorId,
			}).then(o => {
				if(o && o.MFItemIds){
					if(o.MFItemIds.indexOf(",") == -1){
						this.mName.push(o.MFItemIds)
					}else{
						this.mName = o.MFItemIds.split(',')
					}
				}
			})
		}
		this.httpStart()
		
	},
	mounted () {
		
	},
	methods: {
		setStyle(){
			this.arr.map((n,v) => {
				this.mName.map((w,i) => {
					if(n.MFItemId == w){
						this.arr[v].checkedNames = 'success'
						this.MFItemArr.push(this.arr[v])
					}
				})
			})
			this.$bridge.callhandler('printdata',this.arr)
		},
		grouapsFn (MFItemId,MFItemName,Description) {
// 			this.$store.state.grouapsArr = {
// 				DrId: this.DrId,
// 				MFItemId: MFItemId,
// 				MFItemName: MFItemName
// 			}
// 			this.$router.push({ path: '/patient/emphasis/3'})
			this.$router.push({path:'/patient/managementDelete?Diff=2&MFItemId='+MFItemId+'&MFItemName='+MFItemName+'&Description='+Description})
		},
		esc () {
			this.show = false;
		},
		addPatientFn () {
			if(this.MFItemArr.length == 0){
				return this.$toast('请选择分组')
			}else{
				let obj = {}
				if(this.id == undefined){
					return this.$toast('请选择分组')
				}else{
					const List = []
					this.MFItemArr.forEach(v => {
					    List.push({
							MFItemId: v.MFItemId,
							MFItemName: v.MFItemName,
					    })
					})
					obj.parameter = {
						DrId: this.DrId,
						PatientId:this.id,
						MemberfeaturesItems: List
					}
					obj.url = '/PlatFormAPI/Memberfeatures/BacthPatientMemberfeaturesRecord'
					this.$post(obj.url,obj.parameter).then(o => {
						if(o){
							if(this.isImEntry){
								this.$bridge.callhandler('saveSucess', '123')
								this.$bridge.callhandler('BackToIM', '123')
							}else{
								this.$router.back()
							}
							
						}
					})
				}
			}
		},
		circleFn(n,v){
			if(this.arr[n].checkedNames == 'circle'){
				this.MFItemArr.push(v)
				this.arr[n].checkedNames = 'success'
			}else{
				this.MFItemArr.map((w,i) => {
					if(v.MFItemName == w.MFItemName){
						this.MFItemArr.splice(i, 1)	
					}
				})
				this.arr[n].checkedNames = 'circle'
			}
		},
		httpStart () {
			let obj = {}
			obj.url = '/PlatFormAPI/Memberfeatures/QueryDrRecordCountByDrId'
			obj.parameter = {
				"DrId":this.user.Data.User.DoctorId,
				//"OrgIds": this.OrgIds,
				///"PageIndex": 1,
				//"PageSize": 100,
			}
			this.$bridge.callhandler('printdata','初始进入1')
			this.$get(obj.url,obj.parameter).then(o => {
				this.$bridge.callhandler('printdata',o)
				if(o != null){
					this.h404 = false
				}
				if(o){
					o.map((v,n) => {
						o[n].checkedNames = 'circle'
					})
				}
				this.arr = o
				setTimeout(()=>{
					this.setStyle()
				},800)
				
			})
		},
		grouapsDetele (FieldType,MFItemId,MFItemName){ // 文档中删除
			console.log(FieldType)
			this.show = true
			this.FieldType = FieldType
			this.MFItemId = MFItemId
			this.MFItemName = MFItemName
			//let arr = []
			//this.arr.map((v,b) => { b != i ? arr.push(v) : ''})
			//this.arr = arr
		},
		grouapsDeteleDialog (){
			let obj = {}
			obj.url = '/PlatFormAPI/Memberfeatures/DelMemberfeaturesItem'
			obj.parameter = {
				DrId: this.DrId,
				FieldType: this.FieldType,
				MFItemId: this.MFItemId
			}
			this.$get(obj.url,obj.parameter).then(o => {
				this.show = false;
				if(o){
					this.deleteShow = true
					this.httpStart ()
				}
			})
		}
	}
}



